s em geral.\n              Em 2012 foi publicada a Lei n 12.764, de 27 de dezembro de 2012, que instituiu a Poltica Nacional de Proteo dos Direitos da Pessoa com Transtorno do Espectro Autista. Essa lei foi um grande marco para pessoas com TEA e seus familiares.\n              J em 2020, essa lei foi alterada pela Lei n 13.977, de 8 de janeiro de 2020, que instituiu a Carteira de Identificao da Pessoa com Transtorno do Espectro Autista (Ciptea) com o objetivo de garantir ateno integral, pronto atendimento e prioridade no atendimento e no acesso aos servios pblicos e privados, em especial nas reas de sade, educao e assistncia social para a pessoa com Transtorno do Espectro Autista.\n              A Lei estabelece que a CIPTEA deve ser expedida pelos Estados, Distrito Federal e Municpios, e estabelece a necessidade de providenciar a contagem das pessoas com transtorno do espectro autista em todo o territrio nacional. Assim, a Secretaria Nacional dos Diretos da Pessoa com Deficincia lana o Sistema Nacional de emisso da Carteira de Identificao da Pessoa com Transtorno do Espectro Autista.\n              Vale destacar que a emisso da referida carteirinha nacional no substitui a avaliao da deficincia, prevista na Lei Brasileira de Incluso da Pessoa com Deficincia (Lei n 13.146, de 6 de julho de 2015), artigo 2,  1. Entretanto, espera-se que a emisso nacional e padronizada da CIPTEA contribua para a criao de um painel de Business Intelligence que contribuir com o monitoramento e melhoria das polticas pblicas votadas para as pessoas com TEA.\n            ")])])],1)],1)],1),_vm._v(" "),_c('b-card-footer',{staticStyle:{"text-align":"center"}},[_c('row',[_c('b-button',{attrs:{"variant":"info","size":"lg"},on:{"click":function($event){return _vm.navigate('/carteirinha/gerar')}}},[_vm._v("Emitir Carteirinha")]),_vm._v(" "),_c('b-button',{attrs:{"variant":"warning","size":"lg"},on:{"click":function($event){return _vm.navigate('/fale-conosco/cadastrar')}}},[_vm._v("Fale Conosco")]